


















export default {
	data() {
		return {
			news: []
		};
	},
	onLoad() {
		uni.request({
			url: 'https://news-at.zhihu.com/api/4/news/latest',
			method: 'GET',
			data: {},
			success: res => {
				this.news = res.data.stories
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods:{ 
		toInfo(e){
			var newsid = e.currentTarget.dataset.newsid;
			uni.navigateTo({
				url: '../info/info?newsid='+newsid
			});
		}
	},
}



